import React, {useState} from 'react'
import SuperButton from '../hw04/common/c2-SuperButton/SuperButton'
import {restoreState} from '../hw06/localStorage/localStorage'
import s from './Clock.module.css'

function Clock() {
    const [timerId, setTimerId] = useState<number | undefined>(undefined)
    // for autotests //   //      ,     
    const [date, setDate] = useState<Date>(new Date(restoreState('hw9-date', Date.now())))
    const [show, setShow] = useState<boolean>(false)

    const start = () => {
        //   //   (   ,   +1)
        //    (https://learn.javascript.ru/settimeout-setinterval#setinterval)

        const timer = setInterval(() => {
            setDate(new Date(restoreState('hw9-date', Date.now())))
        }, 1000)

        setTimerId(+timer)

    }

    const stop = () => {
        //   //    ,    (timerId <- undefined)
        setTimerId(undefined)
        clearInterval(timerId)
    }

    const onMouseEnter = () => { //   //     
        setShow(!show)
    }
    const onMouseLeave = () => { //   //      
        setShow(!show)
    }

    const dateFormat = (date: number) => date > 9 ? date : `0${date}`

    const stringTime = `${dateFormat(date.getHours())}:${dateFormat(date.getMinutes())}:${dateFormat(date.getSeconds())}` || <br/> // 24:: (01:02:03)/(23:02:03)/(24:00:00)/(00:00:01) //  
    const stringDate = `${dateFormat((new Date()).getDay())}.${dateFormat((new Date()).getMonth() + 1)}.${dateFormat((new Date()).getFullYear())}` || <br/> // .. (01.02.2022) //  ,  01.02.0123/01.02.-123/01.02.12345  

    //    ,    (https://learn.javascript.ru/intl#intl-datetimeformat)
    const stringDay = `${new Intl.DateTimeFormat('en-US', { weekday: 'long' }).format(new Date)}` || <br/> //  
    const stringMonth = `${new Intl.DateTimeFormat('en-US', { month: 'long' }).format(new Date)}` || <br/> //  

    return (
        <div className={s.clock}>
            <div
                id={'hw9-watch'}
                className={s.watch}
                onMouseEnter={onMouseEnter}
                onMouseLeave={onMouseLeave}
            >
                <span id={'hw9-day'}>{stringDay}</span>,{' '}
                <span id={'hw9-time'}>
                    <strong>{stringTime}</strong>
                </span>
            </div>

            <div id={'hw9-more'}>
                <div className={s.more}>
                    {show ? (
                        <>
                            <span id={'hw9-month'}>{stringMonth}</span>,{' '}
                            <span id={'hw9-date'}>{stringDate}</span>
                        </>
                    ) : (
                        <>
                            <br/>
                        </>
                    )}
                </div>
            </div>

            <div className={s.buttonsContainer}>
                <SuperButton
                    id={'hw9-button-start'}
                    disabled={!!timerId} //   //    
                    onClick={start}
                >
                    Start
                </SuperButton>
                <SuperButton
                    id={'hw9-button-stop'}
                    disabled={!timerId} //   //     
                    onClick={stop}
                >
                    Stop
                </SuperButton>
            </div>
        </div>
    )
}

export default Clock
